@import-nomalize;
@import 'helper.scss';

*{margin:0;padding:0;}
*{box-sizing:border-box;}
*::before{box-sizing:border-box;}
*::after{box-sizing:border-box;}
ul,ol{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
body{
  font-family: $font-hei;
  font-size: 16px;
  line-height: 24px;  //最好在开始确定，否则后面改影响范围大
  background-color: #f5f5f5;
}
button,input{
  font: inherit;
}
:focus{
  outline: none;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
.oneLine{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}